import {TOGGLE_CLICK} from '../constants';


const initialState = {sidebarToggle: false};

export default function(state = initialState, action) {
  const newState = Object.assign({}, state);

  switch (action.type) {

  case TOGGLE_CLICK:
    newState.sidebarToggle = !newState.sidebarToggle;
    break;
  default:
    return state;

  }

  return newState;
}



